@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
*{
  margin: 0;
  box-sizing: border-box;
}

body{
  background-color: rgba(34, 112, 147,1.0);
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 20px;
}

#clock{
  display: -webkit-flex;
  display: flex;
  height: 100vh;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#display{
  background-color: rgba(255, 218, 121,1.0);
  max-width: 400px;
  min-width: 400px;
  max-height: 500px;
  min-height: 500px;
  border-radius: 5px;
  padding: 10px;
  border: 2px solid black;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.3);
  
}

#title {
  text-align: center;
}

#lengths{
  padding-top: 30px;
  padding-bottom: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}


#break-label, #session-label{
  border: 2px solid black;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.3);
  padding: 5px;
  border-radius: 5px;
  background-color: rgba(34, 112, 147,0.7);
}

#s-in, #b-in{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  
}

#session-decrement, #break-decrement{
  -webkit-transform: rotate(270deg);
          transform: rotate(270deg);
  cursor: pointer;
  max-width: 20px;
  min-width: 20px;
  max-height: 20px;
  min-heigth: 20px;
}
#session-increment, #break-increment{
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  cursor: pointer;
  max-width: 20px;
  min-width: 20px;
  max-height: 20px;
  min-heigth: 20px;
}

#t-l{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  border: 2px solid black;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.3);
  padding: 5px;
  border-radius: 5px;
  background-color: rgba(34, 112, 147,0.7);
}

#buttons{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

#start_stop, #reset{
  border: 2px solid black;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.3);
  padding: 5px;
  border-radius: 5px;
  margin-right: 20px;
  margin-left: 20px;
  margin-top: 20px;
  background-color: rgba(34, 112, 147,0.7);
  cursor: pointer;
  max-height: 50px;
  min-height: 50px;
  max-width: 50px;
  min-width: 50px;
}

#timer-label{
  text-align: center;
}

#time-left{
  text-align: center;
  font-size: 60px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

